@import "../../utils/constants.scss";

// Valor hexadecimal del color de relleno de los iconos
$icon-fill: '2d2d2d';

.FilminasSlider {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;

    .Filmina {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: contain;
        opacity: 0;
        transition: opacity 2s;

        &.active {
            opacity: 1;
        }
    }

    .footer {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        bottom: 0;
        left: 0;
        width: 100%;
        //background-color: rgba($colorMain, .9);
        color: #fff;
        font-weight: bold;
        font-size: .9rem;
        padding: .5rem 1rem;

        @media (max-width: $tablet-big) {
            & { 
                flex-direction: column;
                justify-content: center;
                padding: .5rem;
            }

            .apps-references, .footer-content {
                //justify-content: center;
                //width: 100% !important;
            }

            .apps-references {
                margin-bottom: .5rem; 

                img {
                    width: 4rem;
                }
            }
        }

        @media (max-width: $desktop) {
            & {
                .apps-references {
                    img {
                        width: 6rem !important;
                    }
                }
            }
        }

        @media (max-width: 1150px) {
            & {
                .apps-references {
                    //width: 30%;

                    img {
                        width: 7rem;
                    }
                }

                .footer-content {
                    //width: 70%;
                    font-size: .75rem;                }
            }
        }

        .apps-references {
            display: flex;
            flex-direction: row;
            //width: 40%;
            animation: showAppReference .2s;

            img {
                max-width: 8rem;
            }

            @keyframes showAppReference {
                from { opacity: 0; }
                to { opacity: 1; }
            }
        }

        .footer-content {
            display: flex;
            flex-direction: column;
            //width: 60%;

            p {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: flex-start;
                text-align: center;
                margin-bottom: 5px;
            }
        }

        .social-icon {
            display: inline-block;
            width: 1.5rem;
            height: 1.5rem;
            background: url('') no-repeat 97% center, #fff;
            background-size: .9rem;
            background-position: center;
            border-radius: 100px;
            padding: .5rem;
            margin-right: 5px;

            &:first-child {
                margin-left: 7px;
            }

